<template>
    <div class="form-container">
        <el-form ref="form" :model="form" :rules="rules" label-width="110px" class="form">
            <el-form-item label="任务id" prop="taskId">
                <el-input v-model="form.taskId"></el-input>
            </el-form-item>
            <el-form-item label="任务加分" prop="addScore">
                <el-input v-model="form.addScore"></el-input>
            </el-form-item>
            <el-form-item label="提前结束任务" prop="subScore">
                <el-radio-group v-model="form.subScore">
                    <el-radio class="block" label="0">无惩罚 </el-radio>
                    <el-radio class="block" label="-1">有惩罚 <el-input v-model="subScoreNum"></el-input></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="是否可重复" prop="isDuplicate">
                <el-radio-group v-model="form.isDuplicate">
                    <el-radio label="Y">是</el-radio>
                    <el-radio label="N">否</el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="备注" prop="memo">
                <el-input type="textarea" v-model="form.memo"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="commit">提交</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import { addTask } from 'api/task';

    export default {
        data() {
            return {
                form: {
                    taskId: '',
                    addScore: '',
                    subScore: '',
                    isDuplicate: '',
                    memo:''
                },
                subScoreNum:'',
                rules: {
                    taskId: [
                        { required: true, message: '请输入任务id', trigger: 'blur' }
                    ],
                    addScore: [
                        { required: true, message: '请输入任务加分,最多输入小数点后两位',pattern:  /^\d+(\.\d{2})?$/, trigger: 'change' }
                    ],
                    subScore:[
                        { required: true, message: '请选择提前结束任务', trigger: 'change'}
                    ],
                    isDuplicate:[
                        { required: true, message: '请选择是否可重复', trigger: 'change'}
                    ]
                }
            }
        },
        mounted() {

        },
        methods: {
            commit() {
                if(this.form.subScore==-1 && this.subScoreNum <= 0){
                    return;
                }
                var param = this.form;
                if(this.form.subScore==-1 && this.subScoreNum > 0){
                    param.subScore = this.subScoreNum;
                }

                this.$refs['form'].validate((valid) => {
                    if (valid) {
                        addTask(param).then(response => {
                            if (response.success) {
                                this.$message({
                                    message: '操作成功',
                                    type: 'success'
                                });
                                this.$refs['form'].resetFields();
                                this.subScoreNum='';
                            } else {
                                this.$notify({
                                    title: '失败',
                                    message: response.message,
                                    type: 'error',
                                    duration: 2000
                                });
                            }
                        })
                    } else {
                        console.log('error submit!');
                        return false;
                    }
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "src/styles/mixin.scss";

    .form-container {
        @include flex;
        @include flex-justify;
        padding: 15px;
        .form {
            width: 500px;
        }
        .el-radio.block{
            display: block;
            margin-left: 0;
        }
        .wrapper{
            position: relative;
            left: 25px;
            top: -20px;
        }

    }
</style>



